.Asset {
  /* height: auto; */
  align-items: center;
  font-size: calc(15px + 4vmin);
  color: rgb(0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 10px 0 10px;
}

.Loading {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
  width: 100%;
  margin: auto;
}

.MessageStripe {
  margin: 50px 10px 10px 10px !important;
}

@media (min-width: 720px) {
  .MessageStripe {
    margin: 50px auto 10px auto !important;
  }
}
.square {
  position: relative;
  width: 50%;
}

.square:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.AssetCard {
  margin-bottom: 10px;
}

.TransactionCard {
  margin-top: 0px;
}
.TimeLine {
  text-align: center;
}

.content {
  position: absolute;
  width: 100%;
  height: 100%;
}

.Text {
  text-align: center;
  background-color: white;
  align-items: center;
  font-size: calc(2px + 4vmin);
  color: rgb(0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.Tab {
  text-align: left;
  background-color: white;
  align-items: center;
  font-size: calc(2px + 4vmin);
  color: rgb(0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

a {
  color: #e20074;
  text-decoration: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Apple {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #1a1a1b;
  color: white;
  font-family: "Josefin Sans", sans-serif;
}

.App-link {
  color: #61dafb;
  font-family: "Josefin Sans", sans-serif;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (min-width: 800px) {
  #asset,
  #messageStripe {
    max-width: 800px;
    margin: auto;
  }
}
